/* Roboto light (normal) */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/roboto300.woff2") format("woff2"), url("../fonts/roboto300.woff") format("woff"); }

/* Roboto medium (normal) */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto500.woff2") format("woff2"), url("../fonts/roboto500.woff") format("woff"); }

/* Roboto heavy (normal) */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/roboto900.woff2") format("woff2"), url("../fonts/roboto900.woff") format("woff"); }

/* retina */
/* reset list */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: 'Roboto';
  font-weight: 300;
  margin: 0 auto;
  min-height: 100vh;
  min-width: 1440px;
  max-width: 1440px;
  display: grid;
  grid-template-columns: 280px 1160px;
  grid-template-rows: 80px 840px min-content;
  gap: 30px; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  display: grid;
  grid-template-columns: 1fr auto 80px;
  grid-template-rows: 80px;
  align-items: center;
  grid-column: 2 / 3; }

.search {
  height: 100%; }

.search__button {
  padding: 0;
  margin: 0;
  margin-right: 12px;
  border: none;
  background-color: transparent;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }

.search__field {
  width: calc(100% - 200px);
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
  padding: 30px 20px;
  border: none;
  color: #B4BEF0; }
  .search__field::placeholder {
    color: #B4BEF0; }
  .search__field:focus {
    outline: 2px solid #b4bef0; }

.search {
  display: flex;
  align-items: center; }

.user-panel {
  display: flex;
  margin-right: 30px;
  cursor: pointer; }

.user-panel__content {
  display: flex;
  align-items: center;
  padding: 20px 0; }

.user-panel__picture {
  margin-right: 12px; }

.user-panel__name {
  margin-right: 20px; }

.notification {
  width: 100%;
  height: 100%; }

.notification__show {
  display: flex;
  width: inherit;
  height: inherit;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  position: relative; }
  .notification__show::before {
    content: '';
    width: 1px;
    height: 40px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    background-color: #E4E8FC; }

.notification__counter {
  position: absolute;
  top: 25px;
  right: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14px;
  height: 14px;
  color: #ffffff;
  font-family: 'Roboto';
  font-weight: 900;
  font-size: 10px;
  line-height: 12px;
  background-color: #FF8F0B;
  outline: 2px solid #ffffff;
  border-radius: 50%; }

.navigation {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  height: 100%; }

.navigation__logo {
  display: block;
  width: max-content;
  margin: 24px 0 20px 30px; }

.navigation__picture {
  display: flex; }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  height: calc(100% - 80px);
  overflow-y: auto; }

.navigation__item--current .navigation__link {
  background-color: #F1F3FE;
  position: relative;
  font-weight: 500; }
  .navigation__item--current .navigation__link::before {
    content: '';
    width: 6px;
    height: 100%;
    background-color: #576BCC;
    position: absolute;
    left: 0;
    top: 0;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px; }

.navigation__item--current .navigation__text {
  font-weight: 500;
  color: #576BCC; }

.navigation__item--current svg {
  stroke: #576BCC; }

.navigation__item--settings {
  margin-top: auto; }

.navigation__link {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 20px 30px; }

.navigation__icon {
  margin-right: 13px; }
  .navigation__icon svg {
    stroke: #576BCC;
    fill: transparent; }

.navigation__text {
  color: #3C425F;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px; }

.main {
  display: grid;
  grid-template-columns: repeat(4, 260px);
  grid-template-rows: 300px 140px 300px;
  gap: 20px;
  grid-column: 2 / -1; }

.slider {
  display: grid;
  grid-column: 1 / -2;
  grid-row: 1 / 2;
  position: relative;
  overflow: hidden; }

.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.slider__item {
  height: inherit;
  color: #ffffff;
  display: grid;
  grid-template-columns: 100%;
  gap: 10px;
  padding: 60px 50px;
  position: relative;
  width: 820px;
  /*
	&--current {
		display: grid;
	}
*/ }

.slider__title {
  max-width: 484px;
  margin: 0;
  font-weight: 900;
  font-size: 28px;
  line-height: 48px; }

.slider__description {
  max-width: 470px;
  margin: 0;
  margin-bottom: 14px;
  font-weight: 300;
  font-size: 18px;
  line-height: 28px; }

.slider__picture {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1; }

.slider__button {
  justify-self: start;
  text-decoration: none;
  color: inherit;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  padding: 10px 20px;
  background-color: #FF8F0B; }

.slider__controls {
  margin: 0;
  padding: 0;
  list-style: none; }

.slider__control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }
  .slider__control--previous {
    left: -32px; }
    .slider__control--previous .slider__navigate {
      justify-content: flex-end; }
    .slider__control--previous svg {
      margin-right: 10px; }
  .slider__control--next {
    right: -32px; }
    .slider__control--next .slider__navigate {
      justify-content: flex-start; }
    .slider__control--next svg {
      margin-left: 10px; }

.slider__navigate {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 50%;
  background-color: #ffffff;
  width: 60px;
  height: 60px;
  padding: 0;
  cursor: pointer; }

.events {
  grid-column: 1 / -2;
  grid-row: 2 / 3; }

.events__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 260px);
  grid-template-rows: min-content;
  gap: 20px; }

.events__item {
  display: grid;
  gap: 4px;
  padding: 20px 30px; }

.events__title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #3C425F; }

.events__text {
  color: #3C425F;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  margin: 0; }

.events__duration {
  color: #0BC4CF;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
  margin-bottom: 6px; }

.events__link {
  color: #576BCC;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  justify-self: end;
  align-self: end; }

.course {
  display: none; }

.lectures {
  display: none; }

.footer {
  grid-column: 1 / -1;
  grid-row: -1 / -2;
  background-color: #3C425F;
  padding: 20px 30px; }

.footer__list {
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  align-items: center;
  grid-template-columns: 40% 60%;
  grid-template-rows: 100%;
  grid-template-areas: 'copyright slogan';
  gap: 20px; }

.footer__element {
  color: rgba(255, 255, 255, 0.5); }
  .footer__element--copyright {
    grid-area: copyright; }
  .footer__element--slogan {
    grid-area: slogan; }

.footer__description-list {
  margin: 0;
  display: flex; }

.footer__description-definition {
  margin: 0; }
